<template>
  <div class="wrapper">
    <div class="title">
      <div class="iconfont title__back" @click="handleBackClick">&#xe6f2;</div>
      <div class="title__text">管理收货地址</div>
      <div class="title__add" @click="handleAddClick">新建</div>
    </div>
    <Address
      v-for="address in addressList"
      :key="address.id"
      :address="address"
      @click="() => handleUpdateClick(address.id)"
    />
  </div>
</template>

<script>
import Address from "../../components/Address";
import useCommonAddressEffect from "../../effects/addressEffect";

import { useUserStore } from "../../store/useUserStore";
import { useRouter } from "vue-router";

export default {
  name: "MyAddressList",
  components: { Address },
  setup() {
    const userStore = useUserStore();
    const router = useRouter();
    const addressList = userStore.addressList;
    const { getAddressList } = useCommonAddressEffect();
    getAddressList(true);
    const handleBackClick = () => {
      router.back();
    };
    const handleAddClick = () => {
      router.push({ name: "UpsertAddress" });
    };
    const handleUpdateClick = addressId => {
      router.push(`/upsertAddress/${addressId}`);
    };
    return { addressList, handleBackClick, handleAddClick, handleUpdateClick };
  }
};
</script>

